import Vue from 'vue'
import Router from 'vue-router'
const home = resolve => require(['@/components/home/home'], resolve)
const list = resolve => require(['@/components/list/list'], resolve)
const lchome = resolve => require(['@/components/lchome/lchome'], resolve)

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'lchome',
      component: lchome
    },
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/list',
      name: 'list',
      component: list
    }
  ]
})



